.button {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 40px;
  font-family: $font-oswald;
  font-size: 14px;
  line-height: 18px;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  color: $color-white;
  background-color: $color-green;

  &--grey {
    color: $color-grey;
    background-color: $color-grey-400;
  }
}
